@import url(https://fonts.googleapis.com/css2?family=Shadows+Into+Light&display=swap);
* {
  font-family: 'Hammersmith One', sans-serif;
  margin: 0;
}
.App {
  background-image: linear-gradient(60deg, #64b3f4 0%, #c2e59c 100%);
  height: 100vh;
  position: relative;
}

.homeScreen {
  position: relative;
}
.homeScreenCard {
  z-index: 1;
  position: relative;
}

.welcomeText {
  position: absolute;
  background-image: linear-gradient(to top, #e6e9f0 0%, #eef1f5 100%);
  display: flex;
  justify-content: center;
  align-items: center;
  top: 8vh;
  left: 10vh;
  width: 40vw;
  height: 20vh;
  border-radius: 1vh;
  font-size: 1.6em;
  box-shadow: 10px 10px 14px 11px rgba(0, 0, 0, 0.52);
}
.imageCard {
  position: absolute;
  height: 70vh;
  width: 30vw;
  z-index: 1;
  top: 8vh;
  right: 5vw;
  background: greenyellow;
  box-shadow: 18px 9px 18px 1px rgba(0, 0, 0, 0.75);
}
.profileCaresol {
  position: absolute;
  top: 35vh;
  z-index: 1;
  left: 1vw;
  width: 55vw;
  height: 40vh;

  background-image: linear-gradient(
    67.2deg,
    rgba(37, 208, 199, 1) -7.5%,
    rgba(165, 90, 240, 1) 62.7%
  );
  box-shadow: 34px 15px 60px -8px rgba(0, 0, 0, 0.33);
  padding: 1vh;
  padding-left: 0;
  border: 0.1vh solid blue;
  border-radius: 2vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.imageContainer {
  height: 24vh;
  width: 53vw;
  object-fit: cover;
  margin: 2vh;
}
@media (max-width: 1000px) {
  .welcomeText {
    left: 7.5vh;
    width: 50vw;
    font-size: 1.4em;
  }
  .profileCaresol {
    top: 42vh;
    width: 60vw;
    font-size: 0.9em;
  }
  .imageCard {
    right: 2vw;
    height: 63vh;
    width: 32vw;
  }
}

@media (max-width: 750px) {
  .welcomeText {
    top: 4vh;
    left: 5vh;
    width: 78vw;
    font-size: 1.2em;
  }
  .imageCard {
    top: 32vh;
    left: 20vw;
    height: 42vh;
    width: 60vw;
    min-width: 45vw;
  }
  .profileCaresol {
    display: none;
  }
}

.loader,
.loader:before,
.loader:after {
  background: #ffffff;
  -webkit-animation: load1 1s infinite ease-in-out;
  animation: load1 1s infinite ease-in-out;
  width: 1em;
  height: 4em;
}
.loader {
  color: #ffffff;
  text-indent: -9999em;
  margin: 88px auto;
  position: relative;
  font-size: 11px;
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.loader:before,
.loader:after {
  position: absolute;
  top: 0;
  content: '';
}
.loader:before {
  left: -1.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.loader:after {
  left: 1.5em;
}
.loaderText {
  color: white;
  font-size: 1.3em;
  font-style: italic;
}
@-webkit-keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}

.cardContainer {
  width: 96%;
  height: 86.5%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  padding: 0.2vh 2vw;
  position: relative;
}
.aboutMeText {
  text-align: left;
  font-family: 'Shadows Into Light', cursive;
  color: #000;
  font-size: 1.3em;
  font-weight: 700;
  width: 75%;
}
.icon-style {
  font-size: 2em;
  font-weight: 700;
  color: white;
}
.gifContainer {
  min-height: 20vh;
  position: absolute;
  left: 0;
  bottom: 0;
}
.gifImage {
  height: 27vh;
  width: 32vw;
}
.software-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 1vh 0;
}
.icon-container {
  height: 12vh;
  width: 9vw;
  background-image: linear-gradient(to top, #09203f 0%, #537895 100%);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0.8vh;
  flex-direction: column;
  margin: 0 1vw;
  box-shadow: 10px 10px 28px -1px rgba(0, 0, 0, 0.75);
}
.titleText {
  font-weight: 700;
  border-bottom: 0.1vh solid black;
  padding-bottom: 0.5vh;
}
@media (max-width: 1000px) {
  .aboutMeText {
    font-size: 1.1em;
    width: 90%;
  }
  .icon-container {
    height: 11vh;
    width: 15vw;
  }
  .gifImage {
    width: 50vw;
  }
}

@media (max-width: 700px) {
  .aboutMeText {
    font-size: 1em;
    width: 100%;
  }
  .icon-container {
    font-size: 0.75em;
    justify-content: space-evenly;
    height: 9vh;
    width: 18vw;
  }
  .gifImage {
    height: 26vh;
    width: 80vw;
  }
}

.navBar {
  display: flex;
  font-size: 1.5em;
  justify-content: space-between;
  z-index: 1000;
  align-items: center;
  background-image: linear-gradient(
    180deg,
    rgba(155, 254, 23, 1) 45.5%,
    rgba(36, 243, 8, 1) 97.9%
  );
  padding: 0;
  margin: 0;
  height: 7vh;
  width: 100%;
  border-bottom: 0.5vh solid rgb(2, 87, 2);
  z-index: 10;
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.34),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048), 0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072), 0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12);
}

.navBarBrand {
  background-image: linear-gradient(
    to right,
    #000000 0%,
    #434343 51%,
    #000000 100%
  );
}
.navBarBrand {
  height: 100%;
  width: 20vw;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  box-shadow: 0 0 20px #eee;

  display: flex;
  align-items: center;
  justify-content: center;
}

.navBarBrand:hover {
  background-position: right center; /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}

.navBarLinks {
  display: flex;
  align-items: center;
}
.navLinks {
  text-decoration: none;
  color: inherit;
}

.navLinkContainer {
  background-image: linear-gradient(
    to right,
    #00c6ff 0%,
    #0072ff 51%,
    #00c6ff 100%
  );
}
.navLinkContainer {
  margin: 1vh;
  width: 9vw;
  font-size: 0.9em;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  text-transform: uppercase;
  transition: 0.8s;

  background-size: 200% auto;
  color: white;
  box-shadow: 0 0 3vh #eee;
  border-radius: 10px;
  box-shadow: 0 10px 6px -6px #777;
}
.dropdown {
  display: none;
}

.navLinkContainer:hover {
  background-position: right center; /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}
@media (max-width: 1300px) {
  .navLinks {
    font-size: 0.8em;
  }
  .navBarBrand {
    width: 27vw;
  }
}
@media (max-width: 1000px) {
  .navLinks {
    font-size: 0.7em;
  }
  .navBarBrand {
    width: 32vw;
  }
  .navLinkContainer {
    width: 12vw;
    height: 3.5vh;
  }
}

@media (max-width: 750px) {
  .navLinks {
    font-size: 0.55em;
  }
  .navBarBrand {
    width: 35vw;
  }
  .navBarLinks {
    display: none;
  }
  .dropbtn {
    background: transparent;
    color: white;
    padding: 13px;
    font-size: 16px;
    border: none;
    position: relative;
  }

  .dropdown {
    position: relative;
    display: inline-block;
  }

  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 5;
    position: absolute;
    top: 5vh;
    right: 2vw;
    width: 45vw;
    font-size: 0.9em;
  }

  .dropdown-content a {
    border-bottom: 0.1vh solid black;
    background-color: #fad961;
    background-image: linear-gradient(90deg, #fad961 0%, #f76b1c 100%);
    color: black;
    padding: 1vh 2vw;
    text-decoration: none;
    display: block;
  }

  .dropdown-content a:hover {
    background-color: #ddd;
  }

  .dropdown:hover .dropdown-content {
    display: block;
  }

  .dropdown:hover .dropbtn {
    background-color: #3e8e41;
  }
  .icon {
    font-size: 1.5em;
    color: #144a14;
    border-bottom: 0.1vh solid black;
    padding-bottom: 0.5vh;
  }
  .icon-contained {
    font-size: 0.9em;
    color: #000000;
  }
}

.footer {
  position: absolute;
  bottom: 0;
  height: 6.5vh;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
  width: 100%;
  box-shadow: 0px -5px 27px -3px rgba(0, 0, 0, 0.75);
  background-image: linear-gradient(
    105.3deg,
    rgba(30, 39, 107, 1) 21.8%,
    rgba(77, 118, 221, 1) 100.2%
  );
}
.footerIcons {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 0.6vh;
  font-size: 1.4em;
}
.footerIcon {
  margin: 0 0.3vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.imageIcon {
  height: 3.6vh;
  width: 3.6vh;
  padding-top: 0.4vh;
}
@media (max-width: 750px) {
  .footer {
    font-size: 0.9em;
  }
  .imaageIcon {
    height: 2.5vh;
    width: 2.5vh;
    padding-top: 0.1vh;
  }
}

.contactForm {
  width: 100%;
  height: 86.5%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.contactFormCard {
  box-shadow: 3px 7px 10px 0px rgba(0, 0, 0, 0.75);
  width: 60vw;
  height: 70vh;
  background-image: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  border-radius: 10%;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.contact-form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: absolute;
  height: 90%;
  width: 60%;
  top: 0;
  left: 0;
  padding: 1vh;
}

.inputContainer {
  margin: 1vh;
}
.inputContainer > input,
textarea {
  padding-left: 1vh;
  width: 50vh;
  font-size: 1.2em;
  line-height: 5vh;
  outline: none;
  resize: none;
  border: 0.05vh solid black;
  border-radius: 1vh;
  box-shadow: 3px 7px 10px 0px rgba(0, 0, 0, 0.75);
}
.inputContainer > textarea {
  height: 20vh;
}
.inputContainer > hr {
  box-shadow: 2px 3px 6px 0px rgba(0, 0, 0, 0.75);
}

.submitButton {
  background-image: linear-gradient(
    to right,
    #00d2ff 0%,
    #3a7bd5 51%,
    #00d2ff 100%
  );
  margin: 1vh;
  padding: 15px 45px;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  box-shadow: 0 0 20px #eee;
  border-radius: 10px;
  display: block;
  box-shadow: 3px 7px 10px 0px rgba(0, 0, 0, 0.75);
}

.submitButton:hover {
  background-position: right center; /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}
.myDetails {
  position: absolute;
  right: 5vh;
  top: 4vh;
  margin: 0 1vh;
  height: 83%;
  flex: 1 1;
  display: flex;

  justify-content: space-around;
  flex-direction: column;
}
.myDetailsItem {
  display: inline;
  padding: 0 1vh;
  border-bottom: 0.1vh solid rgba(0, 0, 0, 0.55);
}
.myDetailsItem > a {
  text-decoration: none;
  color: rgb(255, 0, 0);
}
@media (max-width: 1200px) {
  .contactFormCard {
    width: 70vw;
  }
  .contact-form {
    margin: 0 1.5vh;
    padding: 0;
  }
  .inputContainer {
    margin: 1vh 2vh;
  }
  .inputContainer > input,
  textarea {
    width: 40vh;
  }
}
@media (max-width: 1000px) {
  .myDetails {
    display: none;
  }
}
@media (max-width: 850px) {
  .contactFormCard {
    width: 80vw;
    height: 75vh;
    border-radius: 6%;
  }
  .contact-form {
    width: 100%;
    margin: 0;
  }
  .inputContainer {
    width: auto;
    margin: 0.8vh 0;
  }
  .inputContainer > input,
  textarea {
    width: auto;
  }
}

.errorCardBackground {
  display: flex;

  align-items: center;
  justify-content: center;
  height: 80%;
  width: 100%;
}
.errorCard {
  box-shadow: 10px 10px 13px 0px rgba(0, 0, 0, 0.75);

  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(
    359.8deg,
    rgba(252, 255, 222, 1) 2.2%,
    rgba(182, 241, 171, 1) 99.3%
  );
  width: 80%;
  height: 80%;
}
.errorBTNList {
  position: absolute;
  display: flex;
  align-items: center;
  margin: 2vh;
  width: 60%;
  bottom: 0;
  left: 0;
}
.errorBTN {
  margin: 1vh;
  text-decoration: none;
  height: 7vh;

  width: 9vw;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  box-shadow: 0 0 20px #eee;
  border-radius: 10px;
  font-size: 0.95em;
  box-shadow: 0px 2px 13px 0px rgba(0, 0, 0, 0.75);
  background-image: linear-gradient(
    to right,
    #000000 0%,
    #53346d 51%,
    #000000 100%
  );
}

.errorBTN:hover {
  background-position: right center; /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}
@media (max-width: 1000px) {
  .errorBTN {
    height: 6.5vh;
    width: 12vw;
    font-size: 0.65em;
  }
}

